import { createSlice } from '@reduxjs/toolkit'

const initialState = {
    lexicon: {
        nav: {
            home: '首頁',
            tracklist: '追蹤清單',
            settings: '設置'
        },
        home: {
            newproducts: '新品推薦',
            bestsellers: '暢銷熱賣',
            direct: '前往商品',
            track: '追蹤',
            tracked: '已追蹤',
            search: '搜尋',
            searchAlert: '請輸入關鍵字！',
            notFound: '搜尋結果：目前尚無此書！'
        },
        track: {
            title: '追蹤清單',
            cover: '書封',
            name: '書名',
            price: '價格',
            action: '變更',
            twd: '元',
            direct: '前往商品',
            delete: '刪除',
            deleteAlert: '您確定要刪除嗎?',
            notTrack: '尚無追蹤商品！',
            totalNumber: '追蹤商品總數',
            totalAmount: '總金額',
            clearTracked: '清除已追蹤商品',
            clearConfirm: '確定要清除所勾選的商品嗎?',
            checkAlert: '請先勾選要清除的商品！'
        },
        settings: {
            title: '設置',
            language: '語言',            
            languageFamily: 'zh',
        }
    },
}

// settingsSlice
export const settingsSlice = createSlice({
    name: 'settings',
    initialState,
    reducers: {
        setLanguage: (state, action) => ({...state, lexicon: {...action.payload}})
    }
});

// action creators
export const { setLanguage } = settingsSlice.actions;

// reducer
export default settingsSlice.reducer;